.pages-number {
    margin: 2rem;
    list-style: none;
    display: flex;
    justify-content: center;
}

.pages-number li {
    margin: 2px;
    padding: 10px;
    border: 1px solid black;
    cursor: pointer;
    background-color: aliceblue;
}

.pages-number li:hover {
    background-color: rgb(222, 202, 176);
}

.pages-number li.active {
    width: 80px;
    color: white;
    background-color: rgb(68, 207, 13);
}

.pages-number li.active  div {
    background-color: aqua;
    color: black;
    display: block;
}

.pages-number li.active div:hover {
    background-color: rgb(59, 138, 138);
}

.pages-number li.active div.upper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(68, 207, 13);
    color: white;
}

.pages-number button {
    height: 39px;
    background-color: #b6b1b1; 
    border: none;
    color: white;
    padding: 10px 10px;
    margin: 2px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
}

.pages-number button:hover {
    background-color: #5f5858;
}


@media screen and (max-width: 750px) {
    .non-active {
        display: none;
    }
}

.vertical-menu {
    width: 80px;
    max-height: 0;
    overflow-y: hidden;
    transition: all 0.5s;
}

.vertical-menu-show {
    width: 80px;
    height: 150px;
    overflow-y: auto;
    transition: all 0.5s;
    position:absolute;
}

.pages-number div.non-work {
    display: none;
}


.bg_gradient {

    /* ff 3.6+ */
    background:-moz-linear-gradient(0deg, rgba(23, 23, 24, 1) 85%, rgba(255, 255, 255, 1) 100%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(0deg, rgba(23, 23, 24, 1) 85%, rgba(255, 255, 255, 1) 100%);

    /* opera 11.10+ */
    background:-o-linear-gradient(0deg, rgba(23, 23, 24, 1) 85%, rgba(255, 255, 255, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#171718', endColorstr='#FFFFFF', GradientType=1 );

    /* ie 10+ */
    background:-ms-linear-gradient(0deg, rgba(23, 23, 24, 1) 85%, rgba(255, 255, 255, 1) 100%);

    /* global 94%+ browsers support */
    background:linear-gradient(0deg, rgba(23, 23, 24, 1) 85%, rgba(255, 255, 255, 1) 100%);

}